.no-webp .start {
    background-image: url('/build/img/header.jpg');
}
.webp .start {
    background-image: url('/build/img/header.webp');
}

.header {
    background-color: $darkGrey;
    padding: 1rem 0 3rem 0;

    &.start {
        background-position: center center;
        background-size: cover;

        @include tablet {
            min-height: 70rem;
        }

        .header-content {
            @include tablet {
                min-height: 70rem;
            }
        }
    }
}

.header-content {
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    h1 {
        text-align: left;
        color: $white;
        font-weight: $bold;
        line-height: 2;
        max-width: 60rem;
    }
}